@import url(https://fonts.googleapis.com/css?family=McLaren&display=swap);
@import url(https://fonts.googleapis.com/css?family=Bowlby+One+SC&display=swap);
@import url(https://fonts.googleapis.com/css?family=Bowlby+One+SC&display=swap);
body {
  background-color: transparent;  
}

/* Default React App CSS Styles */
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

/* .Pokeball-logo {
  background-image: url(./img/pokeball.png);
} */

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.wrapper {
    background-color: rgb(168, 206, 227);
    height: 100%;
    width: 100%;
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
    align-content: flex-start;
    overflow: auto;
  }

.header {
    text-align: center;
    height: 100%;
    width: 100%;
    display: flex;
  }
  
  .header-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
    height: 80px;
  }
  
  .header-header {
    background: #222;
    width: 100%;
    text-align: center;
    box-shadow: 0 14px 28px rgba(0,0,0,.25), 0 10px 10px rgba(0,0,0,.22);
    color: #fff;
  }
  
  .header-title {
    font-size: 1.5em;
    /* font-family: 'Holtwood One SC', serif; */
    font-family: 'Bowlby One SC', cursive;
  }
  
  @-webkit-keyframes header-logo-spin {
    from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
  }
  
  @keyframes header-logo-spin {
    from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
    to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
  }
  
.instructionsbox {
    width: 100%;
    text-align: center;
    vertical-align: center;
    font-family: 'McLaren', cursive;
    color: #555555;
    text-shadow: 0 0 5px #ffffff;
    margin-top: 20px;
    margin-bottom: 20px;
}

.instructionsbox h1 {
    font-size: 3.5em;
}

.instructionsbox p {
    font-size: 1.8em;
}

.nav-pills {
    display: block;
    
    background: #222;
    width: 100%;
    text-align: center;
    box-shadow: 0 14px 28px rgba(0,0,0,.25), 0 10px 10px rgba(0,0,0,.22);
    color: #fff;
    font-family: 'Bowlby One SC', cursive;
    font-size: 1.5em;
    font-weight: normal;
    padding-top: 10px;
    padding-bottom: 10px;
    padding-left: 10px;
    padding-right: 10px;
    border-radius: 25px;
}

.nav-pills a {
    color: #fff;
}

.nav-pills a:hover {
    color: #fff;
    background-color: rgb(77, 183, 233) !important;
}

.desc-normal {
    color: #fff;
}

.desc-correct {
    color: rgb(77, 183, 2331);
}

.desc-incorrect {
    color: #f00;
}
.card {
    background: #fff;
    border-radius: 15px;
    height: 160px;
    overflow: hidden;
    margin: 1rem;
    position: relative;
    width: 250px;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    text-align: center;
}

.card > .img-container {
    width: 100%;
    height: auto;
    overflow: hidden;
    text-align: center;
}
.card > .img-container img {
    margin-top: 10px;
    width: 100%;
    height: 140px;
}

/* Shake Feature of Images */
.style_prevu_kit
{
    display: inline-block;
    border: 0;
    width: 200px;
    height: 220px;
    position: relative;
    -webkit-transition: all 200ms ease-in;
    -webkit-transform: scale(1); 
    -ms-transition: all 200ms ease-in;
    -ms-transform: scale(1); 
    -moz-transition: all 200ms ease-in;
    -moz-transform: scale(1);
    transition: all 200ms ease-in;
    transform: scale(1);   
}

.style_prevu_kit:hover
{
    cursor:pointer;
    box-shadow: 0px 0px 150px #000000;
    z-index: 2;
    -webkit-transition: all 200ms ease-in;
    -webkit-transform: scale(1.3);
    -ms-transition: all 200ms ease-in;
    -ms-transform: scale(1.3);   
    -moz-transition: all 200ms ease-in;
    -moz-transform: scale(1.3);
    transition: all 200ms ease-in;
    transform: scale(1.3);
}

.style_prevu_kit_ex {
    -webkit-animation: shake 0.3s;
            animation: shake 0.3s;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
}

@-webkit-keyframes shake {
    0% { -webkit-transform: translate(5px, 0px) rotate(0deg); transform: translate(5px, 0px) rotate(0deg); }
    10% { -webkit-transform: translate(-5px, 0px) rotate(0deg); transform: translate(-5px, 0px) rotate(0deg); }
    20% { -webkit-transform: translate(5px, 0px) rotate(0deg); transform: translate(5px, 0px) rotate(0deg); }
    30% { -webkit-transform: translate(-5px, 0px) rotate(0deg); transform: translate(-5px, 0px) rotate(0deg); }
    40% { -webkit-transform: translate(5px, 0px) rotate(0deg); transform: translate(5px, 0px) rotate(0deg); }
    50% { -webkit-transform: translate(-5px, 0px) rotate(0deg); transform: translate(-5px, 0px) rotate(0deg); }
    60% { -webkit-transform: translate(5px, 0px) rotate(0deg); transform: translate(5px, 0px) rotate(0deg); }
    70% { -webkit-transform: translate(-5px, 0px) rotate(0deg); transform: translate(-5px, 0px) rotate(0deg); }
    80% { -webkit-transform: translate(5px, 0px) rotate(0deg); transform: translate(5px, 0px) rotate(0deg); }
    90% { -webkit-transform: translate(-5px, 0px) rotate(0deg); transform: translate(-5px, 0px) rotate(0deg); }
    100% { -webkit-transform: translate(5px, 0px) rotate(0deg); transform: translate(5px, 0px) rotate(0deg); }
}

@keyframes shake {
    0% { -webkit-transform: translate(5px, 0px) rotate(0deg); transform: translate(5px, 0px) rotate(0deg); }
    10% { -webkit-transform: translate(-5px, 0px) rotate(0deg); transform: translate(-5px, 0px) rotate(0deg); }
    20% { -webkit-transform: translate(5px, 0px) rotate(0deg); transform: translate(5px, 0px) rotate(0deg); }
    30% { -webkit-transform: translate(-5px, 0px) rotate(0deg); transform: translate(-5px, 0px) rotate(0deg); }
    40% { -webkit-transform: translate(5px, 0px) rotate(0deg); transform: translate(5px, 0px) rotate(0deg); }
    50% { -webkit-transform: translate(-5px, 0px) rotate(0deg); transform: translate(-5px, 0px) rotate(0deg); }
    60% { -webkit-transform: translate(5px, 0px) rotate(0deg); transform: translate(5px, 0px) rotate(0deg); }
    70% { -webkit-transform: translate(-5px, 0px) rotate(0deg); transform: translate(-5px, 0px) rotate(0deg); }
    80% { -webkit-transform: translate(5px, 0px) rotate(0deg); transform: translate(5px, 0px) rotate(0deg); }
    90% { -webkit-transform: translate(-5px, 0px) rotate(0deg); transform: translate(-5px, 0px) rotate(0deg); }
    100% { -webkit-transform: translate(5px, 0px) rotate(0deg); transform: translate(5px, 0px) rotate(0deg); }
}

